




































import Vue from 'vue';

// Pruebas
const appendLevel = {
    1: 'ero',
    2: 'do',
    3: 'ero',
    4: 'to',
    5: 'to',
    6: 'to',
    7: 'mo',
    8: 'vo',
    9: 'no',
};

export default Vue.extend({
    name: 'RomaUnit',
    mounted () {
      this.expanded = this.expand;
    },
    props: {
        md: { // Propiedad que mueve el tamaño en "display medium" de la primera columna/celda del componente
          type: Number,
          default: 7
        },
        unit: {
            type: Object,
            required: true,
        },
        expand: {
            type: Boolean,
            default: true,
        },
        menu: {
          type: Array,
          default() {
              return [
                  {label: 'Editar', action: null, route: null},
                  {label: 'Eliminar', action: null, route: null},
                  {label: 'Vista detalles', action: null, route: null},
              ];
          },
        },
        index: Number,
    },
    data() {
        return {
            expanded: true,
        };
    },
    computed: {
      rowMd () {
          if (!this.expanded) return 12;
          else return this.md;
      }
    },
    methods: {
        appendToLevel(level) {
            return appendLevel[level];
        },
    },
});
