











































































































import Vue from "vue";

import UnitPlanOa from "@/models/UnitPlanOa";

export default Vue.extend({
    name: "RomaAdvance",
    created () {
      this.selectedMenu = this.menu;
    },
    props: {
        unit: {
            type: Object,
            required: true,
        },
        height: {
            type: String,
            default: '514'
        },
        applyRowColor: {
            type: Boolean,
            default: true
        },
    },
    data() {
        return {
            expanded: [],
            request: null, // modificación del ie
            headers: [
                { text: '1', value: '1' },
                { text: '2', value: '2' },
                { text: '3', value: '3' },
                { text: '4', value: '4' },
                { text: '5', value: '5' },
            ],
        };
    },
    computed: {
        oasAdded () {
            return UnitPlanOa.query()
                .where('unitplan_id', this.unit.id)
                .where('disabled', false)
                .with('oa', query => {
                    query.with('ies')
                })
                .get();
        },
    },
    methods: {
        saveAdvance () {
            const msj = {
                show: true,
                text: '¡Cobertura de la unidad Guardada!',
                color: 'success',
            };

            this.$store.state.snackbar = msj;
        },
        ieStyle (done) {
            const colors = ['primary', 'secondary', 'roma_rcolor', 'roma_acolor'];
            const index = Math.floor(Math.random() * (colors.length - 0)) + 0;

            if (done) return colors[index];
            else return '';

        },
        rowStyle (itemIndex: number, disabled: boolean) {
            const colors = ['--v-roma_rcolor-base', '--v-primary-base', '--v-secondary-base', '--v-roma_acolor-base'];
            let color = colors[itemIndex];
            color = `var(${colors[itemIndex % colors.length]})`;

            if (disabled || !this.applyRowColor) color = 'grey';

            return `border-left: 8px solid ${color};`;
        }
    },
});
