











































import Vue from 'vue';
import RomaBreadcrumbs from '@/components/partial/RomaBreadcrumbs.vue';
import RomaUnit from '@/components/RomaUnit.vue';
import UnitPlan from '@/models/UnitPlan';
import RomaAdvance from "@/components/RomaAdvance.vue";

export default Vue.extend({
    name: 'OrganizerUnit',
    beforeRouteEnter (to, from, next) {
        //FIXME: Problemas con unidades con indice > 1
        let unit = UnitPlan.find(to.params.id);

        unit = UnitPlan.query().
                with('details').
                with('coverage').
                whereId(unit.id).
                first();

        next(vm => vm.setUnit(unit));

    },
    data() {
        return {
            unit: null,
        };
    },
    methods: {
        dataReady () {
            return this.unit; // Fix render component with unit prop
        },
        setUnit (unit) {
            this.unit = unit;
        },
    },
    components: {
        RomaBreadcrumbs,
        RomaUnit,
        RomaAdvance,
    }
});
